import React, { useState, useEffect } from "react";
import {
  TextField,
  DefaultButton,
  PrimaryButton,
  ProgressIndicator,
  IconNames,
  Image
} from "office-ui-fabric-react";

import { Toggle } from "office-ui-fabric-react/lib/Toggle";

import { ColorPicker } from "office-ui-fabric-react/lib/ColorPicker";
import { Pivot, PivotItem } from "office-ui-fabric-react/lib/Pivot";
import PropTypes from "prop-types";
import HTMLEditor from "../../components/HTMLEditor";
import IconExplorer from "../../components/IconExplorer";
import ImagePicker from "../../components/ImagePicker";

export default function TileEditor(props) {
  const [tile, setTile] = useState(
    props.tile ? props.tile : { color: "#990022", textcolor: "#dddddd" }
  );
  const [activeTab, setActiveTab] = useState("details");
  const [dialog, setDialog] = useState("");
  const [isDirty, setIsDirty] = useState(false);
  const [isContentEdited, setIsContentEdited] = useState(false);
  const [refresh, setRefresh] = useState(0);
  const TABS = [
    {
      key: "details",
      name: "Details"
    },
    {
      key: "icon",
      name: "Icon"
    },
    {
      key: "color",
      name: "Color"
    },
    {
      key: "article",
      name: "Article"
    },
    {
      key: "status",
      name: "Status"
    },
    {
      key: "features",
      name: "Features"
    },
    {
      key: "code",
      name: "Code"
    }
  ];
  useEffect(() => {}, [refresh]);
  const SQUARE = 128;
  return (
    <div>
      <div style={{ display: "flex" }}>
        <div>
          <div
            style={{
              height: SQUARE,
              width: SQUARE,
              backgroundColor: tile.color,
              margin: 16,
              position: "relative"
            }}
          >
            <div style={{ position: "absolute", top: 0, width: SQUARE }}>
              {tile.title && (
                <div
                  style={{
                    fontSize: 22,
                    xlineHeight: 22,
                    color: tile.textcolor,
                    xwhiteSpace: "nowrap",
                    overflow: "hidden"
                  }}
                >
                  {tile.title}
                </div>
              )}
              {tile.subTitle && (
                <div
                  style={{
                    fontSize: 12,
                    xlineHeight: 12,
                    color: tile.textcolor,
                    xwhiteSpace: "nowrap",
                    overflow: "hidden"
                  }}
                >
                  {tile.subTitle}
                </div>
              )}
            </div>
            <div style={{ position: "absolute", top: 0 }}>
              <img
                style={{
                  margin: 32,
                  width: 64, // - (tile.title ? 20 : 0),
                  height: "auto"
                }}
                src={tile.icon}
              />
            </div>
            <div style={{ position: "absolute", bottom: 0, width: SQUARE }}>
              {tile.inShort && (
                <div
                  style={{
                    fontSize: 14,
                    xlineHeight: 14,
                    color: tile.textcolor,

                    overflow: "hidden"
                  }}
                >
                  {tile.inShort}
                </div>
              )}
            </div>
          </div>
        </div>
        <div style={{ flexGrow: 1 }}>
          <Pivot
            style={{
              paddingLeft: 20,
              borderBottom: "1px solid #eeeeee"
            }}
            onLinkClick={item => {
              setActiveTab(item.props.itemKey);
            }}
            selectedKey={activeTab}
          >
            {TABS.map((tab, key) => {
              return <PivotItem itemKey={tab.key} headerText={tab.name} />;
            })}
          </Pivot>
          <div>
            {activeTab === "details" && renderDetails()}
            {activeTab === "icon" && renderIcon()}
            {activeTab === "color" && renderColor()}
            {activeTab === "status" && renderStatus()}
            {activeTab === "features" && renderFeatures()}
            {activeTab === "article" && renderArticle()}
          </div>
        </div>
      </div>
      {dialog === "upload" && (
        <div>
          <ImagePicker
            onDismiss={() => {
              setDialog("");
            }}
            onSelect={url => {
              setDialog("");
              tile.icon = url;
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
      )}
      {JSON.stringify(tile)}
    </div>
  );
  function renderIcon() {
    return (
      <div>
        <div
          style={{
            padding: "4px",
            paddingLeft: "8px",
            display: "flex"
          }}
        >
          <div style={{ flexGrow: "1" }}>
            <TextField
              placeholder="Select from our list, upload your own or paste in any address"
              value={tile.icon}
              ariaLabel="Icon"
              onChange={(e, v) => {
                tile.icon = v;
                setTile(tile);
                setIsDirty(true);
                setRefresh(refresh + 1);
              }}
            />
          </div>
          <div>
            <DefaultButton
              onClick={() => {
                setDialog("upload");
              }}
              text="Upload"
            />
          </div>
        </div>
        <div className="pivotContent">
          <IconExplorer
            supressBackgroundTester
            onClick={icon => {
              tile.icon = icon;
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
      </div>
    );
  }
  function renderArticle() {
    return (
      <div>
        <HTMLEditor
          height="420px"
          registerEditor={editorReference => {
            //   this.setState({ editorReference });
            //   if (this.props.registerEditorReference) {
            //     this.props.registerEditorReference(editorReference);
            //   }
          }}
          onChange={(delta, oldDelta, source) => {
            setIsContentEdited(true);
          }}
          editable={true}
          associateTileWithUrl={(x, y, contentRef) => {
            return new Promise((resolve, reject) => {
              debugger;
              // this.setState({ contentRef });
              // if (this.props.onChange) {
              //   this.props.onChange({ contentRef });
              // }
              resolve();
            });
          }}
          // domain={domain}
          contentRef={tile.contentRef}
        />
        <TextField
          label="External Article"
          placeholder="URL that opens when the tile is clicked"
          value={tile.externalArticle}
          ariaLabel="External link"
          onChange={(e, v) => {
            tile.externalArticle = v;
            setTile(tile);
            setIsDirty(true);
            setRefresh(refresh + 1);
          }}
        />
        {tile.externalArticle && (
          <a href={tile.externalArticle} target="_blank">
            Test
          </a>
        )}
      </div>
    );
  }

  function renderFeatures() {
    return (
      <div style={{ padding: "16px" }}>
        <div>
          <Toggle
            checked={tile.isFullyShareable}
            label="Fully shareable"
            onText="On"
            offText="Off"
            onChange={(e, on) => {
              tile.isFullyShareable = on;
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
        <div>
          <Toggle
            checked={tile.isPartlyShareable}
            label="Partly shareable"
            onText="On"
            offText="Off"
            onChange={(e, on) => {
              tile.isPartlyShareable = on;
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
        <div>
          <Toggle
            checked={tile.isPremium}
            label="Premium"
            onText="On"
            offText="Off"
            onChange={(e, on) => {
              tile.isPremium = on;
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
      </div>
    );
  }

  function renderStatus() {
    return (
      <div style={{ padding: "16px" }}>
        <div>
          <Toggle
            checked={tile.isHashed}
            label="Hashed"
            onText="Yes"
            offText="On"
            onFocus={() => console.log("onFocus called")}
            onBlur={() => console.log("onBlur called")}
            onChange={(e, on) => {
              tile.isHashed = on;
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
        <div></div>
      </div>
    );
  }

  function renderColor() {
    return (
      <div style={{ display: "flex" }}>
        <div>
          <div style={{ marginLeft: "16px" }}>Background</div>
          <ColorPicker
            color={tile.color}
            onChange={(e, color, y) => {
              tile.color = color.str;
              setRefresh(refresh + 1);
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
        <div>
          <div style={{ marginLeft: "16px" }}>Text</div>
          <ColorPicker
            color={tile.textcolor}
            onChange={(e, color, y) => {
              tile.textcolor = color.str;
              //setRefresh(refresh+1)
              setTile(tile);
              setIsDirty(true);
              setRefresh(refresh + 1);
            }}
          />
        </div>
      </div>
    );
  }

  function renderDetails() {
    return (
      <div style={{ padding: "16px" }}>
        <TextField
          label="Title"
          placeholder="Short, descriptive title"
          value={tile.title}
          ariaLabel="Title"
          onChange={(e, v) => {
            tile.title = v;
            setTile(tile);
            setIsDirty(true);
            setRefresh(refresh + 1);
          }}
        />

        <TextField
          label="Subtitle"
          placeholder="Short qualifier, useful for indicating App is in preview or retiring soon"
          value={tile.subTitle}
          ariaLabel="Sub Title"
          onChange={(e, v) => {
            tile.subTitle = v;
            setTile(tile);
            setIsDirty(true);
            setRefresh(refresh + 1);
          }}
        />
        <TextField
          label="Description"
          placeholder="Short explanation about the App"
          value={tile.inShort}
          ariaLabel="In short"
          multiline
          rows={3}
          onChange={(e, v) => {
            tile.inShort = v;
            setTile(tile);
            setIsDirty(true);
            setRefresh(refresh + 1);
          }}
        />

        <TextField
          label="Link"
          placeholder="URL that opens when the launch icon is clicked"
          value={tile.jumpto}
          ariaLabel="External link"
          onChange={(e, v) => {
            tile.jumpto = v;
            setTile(tile);
            setIsDirty(true);
            setRefresh(refresh + 1);
          }}
        />
        {tile.jumpto && (
          <a href={tile.jumpto} target="_blank">
            Test
          </a>
        )}
        <TextField
          label="Tag"
          placeholder="Text that goes into the URL of the associated App Article"
          value={tile.key}
          ariaLabel="Key"
          onChange={(e, v) => {
            tile.key = v;
            setTile(tile);
            setIsDirty(true);
            setRefresh(refresh + 1);
          }}
        />
      </div>
    );
  }
}

TileEditor.propTypes = {};
